import {TimerType} from "../types";

export function checkNotificationsSupport():void{
    if (!("Notification" in window)) {
        alert("    !");

        return;
    }

    if (Notification.permission === "granted") {
        return;
    }

    if (Notification.permission !== "denied") {
        Notification.requestPermission();
    }
}

function showNotify(title:string, body:string, icon:'red'|'green'):void{
    new Notification(title, {silent:false, body:body, icon:icon === 'red'?'redCircle.png':'greenCircle.png'});
}

export function showDoneTimerNotify(prev:TimerType, next:TimerType):void{
    let title:string = '';
    let body:string = '';

    switch (prev) {
        case TimerType.Pomidor:
            title = ' ';
            break;
        case TimerType.ShortBreak:
            title = '  ';
            break;
        case TimerType.LongBreak:
            title = '  ';
            break;
    }

    switch (next) {
        case TimerType.Pomidor:
            body = '  ';
            break;
        case TimerType.ShortBreak:
            body = '  ';
            break;
        case TimerType.LongBreak:
            body = '  ';
            break;
    }

    showNotify(title, body, prev === TimerType.Pomidor?'green':'red');
}